import React, { Component, ReactElement } from "react";
import * as axios from "axios";

// Template components
import { LinksBlock } from "./template/LinksBlock";
import { Text } from "./template/Text";
import { ImageBlock } from "./template/ImageBlock";
import { CodeBlock } from "./template/CodeBlock";
import { Title } from "./template/Title";
import { ThemesItem } from "./template/ThemesItem";

interface paginationButton {
  constructor: Function;
  create(): void;
  getButton(): JSX.Element;
}

abstract class AbstractPage extends Component {
  public props: object;
  private type: string;

  public constructor(props: object) {
    super(props);
    this.props = props;
    this.type = props["type"];
  }

  protected setPage = (pageId?: number): void => {
    axios["get"](this.props["api"].getPageUrl(this.type, pageId || false)).then(
      (response: object) => {
        if (response["data"]) {
          this.props["actionCreator"]("SET_CURRENT_PAGE", response["data"][0]);
        }
      }
    );
  };

  protected setPageSearcherValue = (newValue: number | "") => {
    this.props["actionCreator"]("SET_PAGE_SEARCHER_VALUE", newValue);
  };
}

class PaginationButton implements paginationButton {
  private type: "previous" | "next";
  private switchingPageId: number;
  private button: JSX.Element;
  private buttonContent: string;
  private onClick: Function | false;

  public constructor(type: "previous" | "next", switchingPageId: number, onClick?: Function) {
    this.type = type;
    this.button = <div></div>;
    this.onClick = onClick || false;

    if (type === "previous") {
      this.buttonContent = "Previous";
      this.switchingPageId = switchingPageId - 1;
    } else {
      this.buttonContent = "Next";
      this.switchingPageId = switchingPageId + 1;
    }
  }

  public create = (): void => {
    this.button = (
      <div
        className={`tp-pagination_button ${this.type}-button`}
        onClick={(): void => {
          if (this.onClick) {
            this.onClick(this.switchingPageId);
          }
        }}
      >
        <a href="#">
          <span>{this.buttonContent}</span>
        </a>
      </div>
    );
  };

  public getButton = (): JSX.Element => {
    return this.button;
  };
}

class ThemesBlock {
  private themes: JSX.Element;
  private items: object;

  public constructor(items: object) {
    this.themes = <div></div>;
    this.items = items;
  }

  public createThemesItem = (themes: Object): JSX.Element[] => {
    if (themes === undefined) {
      return [];
    }

    let iterableThemes = themes[0] || [];
    return iterableThemes.map(
      (theme: object, index: number): JSX.Element => {
        return (
          <ThemesItem
            {...{
              link: {
                link: theme["items"].title.link,
                value: theme["items"].title.value,
                key: index,
              },
              subitems: theme["items"].items,
            }}
          />
        );
      }
    );
  };

  public create = (): void => {
    this.themes = (
      <React.StrictMode>
        <div className="main-container">
          <div className="main-item">
            <div className="tp-themes-block">
              <ul className="tp-themes-list" key={new Date().getTime()}>
                {this.createThemesItem([this.items])}
              </ul>
            </div>
          </div>
        </div>
      </React.StrictMode>
    );
  };

  public getThemes = (): JSX.Element => {
    return this.themes;
  };
}

abstract class PageEngine extends AbstractPage {
  public constructor(props: object) {
    super(props);
  }

  protected createPage = (pageItems: Array<object>): Array<object> | void => {
    try {
      let page: Array<object> = pageItems.map((item: object): object => {
        return this.createElement(item);
      });
      return page;
    } catch (e) {
      this.props["logger"].log(e);
    }
  };

  private addNewText = (source: string, id: number): void | never => {
    axios["get"](source).then((response: object) => {
      this.props["actionCreator"]("ADD_NEW_TEXT", {
        name: id,
        text: response["data"],
      });
    });
  };

  private createElement = (pageElement: object): JSX.Element | {} => {
    if (pageElement["name"] === "title") {
      return <Title value={pageElement["value"]} />;
    } else if (pageElement["name"] === "imageBlock") {
      return (
        <ImageBlock
          image={pageElement["items"].image}
          id={pageElement["items"].id}
          sublinks={[...pageElement["items"].sublinks]}
        />
      );
    } else if (pageElement["name"] === "codeBox") {
      return (
        <CodeBlock
          lines={[...pageElement["items"].codeLines]}
          result={[...pageElement["items"].resultLines]}
          highlight={this.props["syntaxHighlight"].highlight}
        />
      );
    } else if (pageElement["name"] === "themes") {
      const themesBlock = new ThemesBlock(pageElement["items"]);
      themesBlock.create();
      return themesBlock.getThemes();
    } else if (pageElement["name"] === "links") {
      return <LinksBlock links={[...pageElement["items"]]} />;
    } else if (pageElement["name"] === "text") {
      if (Object.keys(this.props["texts"]).length === 0) {
        this.addNewText(pageElement["source"], pageElement["id"]);
      }

      return <Text value={[this.props["texts"][pageElement["id"]]]} id={pageElement["id"]} />;
    } else {
      return {};
    }
  };

  protected createPagination = (pageId: number, nextPage: boolean) => {
    let previous,
      next: ReactElement | string = "";

    if (!(pageId < 2)) {
      let previousButton;

      if (typeof pageId === "string") {
        previousButton = new PaginationButton("previous", pageId);
      } else {
        previousButton = new PaginationButton("previous", pageId, this.setPage);
      }
      previousButton.create();
      previous = previousButton.getButton();
    }

    if (nextPage) {
      const nextButton = new PaginationButton("next", pageId, this.setPage);
      nextButton.create();
      next = nextButton.getButton();
    }

    return (
      <div className="tp-pagination_block">
        {previous}
        {next}
        <div className="tp-page-searcher">
          <input
            type="text"
            id="pageSearcher"
            placeholder="Page number"
            value={this.props["pageSearcherValue"]}
            onChange={(e): void => this.setPageSearcherValue(Number(e.target.value) || "")}
          />
          <div
            className="tp-page-searcher_button"
            onClick={(): void => this.setPage(this.props["pageSearcherValue"])}
          >
            <a href="#">
              <span>Search</span>
            </a>
          </div>
        </div>
      </div>
    );
  };
}

export abstract class Page extends PageEngine {
  public constructor(props: object) {
    super({ ...props });
  }

  protected getInitialPage = (id?: number) => {
    this.setPage(id);
  };

  protected turnOnPreloader = () => {
    this.props["actionCreator"]("SET_PRELOADER_MODE", "LOADING");
  };

  protected turnOffPreloader = () => {
    this.props["actionCreator"]("SET_PRELOADER_MODE", "DISABLED");
  };
}
