import React from "react";
import { Page } from "./Page";
import { Preloader } from "../Preloader";

export class HomePage extends Page {
  public constructor(props: object) {
    super({ ...props, type: "homePage" });
  }

  private toggleMinimap = (): void => {
    if (this.props["minimap"].mode === "minimap") {
      this.props["actionCreator"](
        "SWITCH_MINIMAP_MODE", // Turn on the minimap and get the button checked
        {
          minimapClassList: "minimap minimap_active",
          buttonClassList: "minimapButton_active",
        }
      );
    } else {
      this.props["actionCreator"]("SWITCH_MINIMAP_MODE", {
        minimapClassList: "minimap",
        bottonClassList: "minimapButton",
      });
    }
  };

  public componentDidMount = (): void => {
    try {
      this.turnOnPreloader();
      this.getInitialPage(1);
      this.turnOffPreloader();
    } catch (e) {
      this.props["logger"].log(e);
    }
  };

  public render = (): JSX.Element => {
    return (
      <div>
        <div
          id="minimapButton"
          className={this.props["minimap"].buttonClassList}
          onClick={(): void => this.toggleMinimap()}
        >
          <span>MINIMAP</span>
        </div>
        <div className="main-container">
          <span className="tp-page-id">Page id: {this.props["currentPageId"]}</span>
          <div className="main-item">
            <Preloader
              {...{
                preloader: this.props["images"].preloader,
                mode: this.props["preloaderMode"],
              }}
            />
            {this.createPage(this.props["currentPage"])}
            {this.createPagination(this.props["currentPageId"], this.props["nextPage"])}
          </div>
        </div>
      </div>
    );
  };
}
